<template>
    <div class="slide-container">
        <el-aside width="240px" style="margin-right: 15px;">
            <el-row>
                <el-col>
                    <div class="menuHeader">你好！18358487682</div>
                    <el-menu
                            :default-active="presentPath($route.path)"
                            router
                            class="slideBarBody"
                            @open="handleOpen"
                            @close="handleClose">
                        <el-menu-item index="/companyAuth">
                            <span slot="title">企业认证</span>
                        </el-menu-item>
                        <el-menu-item index="/progressQuery">
                            <span slot="title">进度查询</span>
                        </el-menu-item>
                        <el-menu-item index="/contractQuery">
                            <span slot="title">合同签订/查阅</span>
                        </el-menu-item>
                        <el-menu-item index="/mineLoans">
                            <span slot="title">我的借款</span>
                        </el-menu-item>
                        <el-menu-item index="/withDraw">
                            <span slot="title">提款</span>
                        </el-menu-item>
                        <el-menu-item index="/repayment">
                            <span slot="title">还款</span>
                        </el-menu-item>
                    </el-menu>
                </el-col>
            </el-row>
        </el-aside>
        <slot></slot>
    </div>
</template>


<script>
    import ElMenu from '../../node_modules/element-ui/packages/menu/src/menu.vue';

    export default {
        components: {ElMenu},
        name: 'SlideBar',
        data() {
            return {};
        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            presentPath(path = '/companyAuth') {
                return path;
            }
        }
    };
</script>

<style lang="less" scoped>
    .slide-container {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        .menuHeader {
            background-color: #4f9EFF;
            height: 70px;
            text-align: center;
            line-height: 70px;
            color: #ffffff;
            font-size: 18px;
            font-weight: bold;
        }
        .slideBarBody {
            background-color: #fafafa;
            min-height: 580px;
            border: 1px solid #ececec;
        }
    }
    .el-menu-item{
        font-size: 16px;
        font-weight: 400;
    }
    .el-menu-item.is-active {
        background-color: #d3dce6!important;
    }
</style>


